.contacts-hero {
  padding-top: 92px;
  padding-bottom: 20px;
}
@media screen and (min-width: 768px) {
  .contacts-hero {
    padding-top: 104px;
  }
}
@media screen and (min-width: 1440px) {
  .contacts-hero {
    padding-top: 190px;
    padding-bottom: 50px;
  }
}
.contacts-hero__container {
  position: relative;
}

.contacts-hero__header {
  margin-bottom: 16px;
  font-weight: 700;
  font-size: 24px;
  line-height: 1.25;
  text-transform: uppercase;
  color: #487996;
}

@media screen and (min-width: 768px) {
  .contacts-hero__header {
    font-weight: 700;
    font-size: 36px;
  }
}
@media screen and (min-width: 1440px) {
  .contacts-hero__header {
    margin-left: 134px;
    font-size: 48px;
  }
}

@media screen and (min-width: 768px) {
  .contacts-hero__tumb {
    display: flex;
    gap: 32px;
  }
}

.contacts-hero__video {
  text-align: center;

  @media screen and (max-width: 768px) {
    margin-bottom: 16px;
  }
}

@media screen and (min-width: 1440px) {
  .contacts-hero__video {
    margin-left: 134px;
  }
}

.contacts-hero_picture {
  margin-bottom: 16px;
}

@media screen and (min-width: 1440px) {
  .contacts-hero__text {
    position: relative;
    width: 515px;
  }
}

.contacts-hero__par {
  margin-bottom: 16px;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.25;

  color: #2b2b2b;
}
@media screen and (min-width: 1440px) {
  .contacts-hero__par {
    margin-bottom: 24px;
    font-weight: 300;
    font-size: 18px;
    line-height: 1.27;
  }
}
.contacts-hero__par:last-child {
  margin-bottom: 0;
}

.contacts-hero__span {
  font-size: 22px;
}
@media screen and (min-width: 1440px) {
  .contacts-hero__span {
    font-size: 49px;
    position: absolute;
    bottom: 0;
  }
}

iframe {
  border-radius: 15px;
  width: 270px;
  height: 171px;
}

@media screen and (min-width: 768px) {
  iframe {
    width: 336px;
    height: 261px;
  }
}

@media screen and (min-width: 1440px) {
  iframe {
    width: 555px;
    height: 315px;
  }
}

.figure1 {
  position: absolute;
  z-index: -2;
  background-color: #ffffff;
  top: 200px;
  left: -29px;
  width: 73.65px;
  height: 73.65px;
  border-radius: 0px 0px 35px 0px;
  box-shadow: 20px 20px 80px rgba(0, 0, 0, 0.1);

  @media screen and (min-width: 768px) and (max-width: 1439px) {
    display: none;
  }

  @media screen and (min-width: 1400px) {
    width: 82px;
    height: 78px;
    border-radius: 0px 0px 72px 0px;
    top: 0;
    left: 35px;
  }
}

.figure2 {
  @media screen and (min-width: 768px) {
    position: absolute;
    z-index: -2;
    background-color: #ffffff;
    top: -22px;
    right: 32px;
    width: 71.41px;
    height: 63.79px;
    border-radius: 0px 0px 0px 35px;
    box-shadow: 20px 20px 80px rgba(0, 0, 0, 0.1);
  }

  @media screen and (min-width: 1440px) {
    width: 137px;
    height: 83px;
    border-radius: 0px 0px 0px 72px;
    top: -38px;
  }
}
